/* Boton proceso común */
.checkbox-wrapper-13{
    display: flex;
    align-items: center;
}

  @supports (-webkit-appearance: none) or (-moz-appearance: none) {
    .checkbox-wrapper-13 input[type=checkbox] {
      --active: #66bb6a;
      --active-inner: #fff;
      --focus: 2px #258c2b;
      --border: #b0b0b0;
      --border-hover: #44d94c;
      --background: #fff;
      --disabled: #F6F8FF;
      --disabled-inner: #E1E6F9;
      -webkit-appearance: none;
      -moz-appearance: none;
      height: 15px;
      outline: none;
      display: inline-block;
      vertical-align: top;
      position: relative;
      margin: 0;
      cursor: pointer;
      border: 2px solid var(--border);
      border: 2px solid var(--bc, var(--border));
      background: var(--background);
      background: var(--b, var(--background));
      transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
    }
    .checkbox-wrapper-13 input[type=checkbox]:after {
      content: "";
      display: block;
      left: 0;
      top: 0;
      position: absolute;
      transition: opacity 0.2s, -webkit-transform 0.3s ease;
      transition: transform 0.3s ease, opacity 0.2s;
      transition: transform 0.3s ease, opacity 0.2s, -webkit-transform 0.3s ease;
      transition: opacity var(--d-o, 0.2s), -webkit-transform var(--d-t, 0.3s) var(--d-t-e, ease);
      transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
      transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s), -webkit-transform var(--d-t, 0.3s) var(--d-t-e, ease);
    }
    .checkbox-wrapper-13 input[type=checkbox]:checked {
      --b: var(--active);
      --bc: var(--active);
      --d-o: .3s;
      --d-t: .6s;
      --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
    }
    .checkbox-wrapper-13 input[type=checkbox]:disabled {
      --b: var(--disabled);
      cursor: not-allowed;
      opacity: 0.9;
    }
    .checkbox-wrapper-13 input[type=checkbox]:disabled:checked {
      --b: var(--disabled-inner);
      --bc: var(--border);
    }
    .checkbox-wrapper-13 input[type=checkbox]:disabled + label {
      cursor: not-allowed;
    }
    .checkbox-wrapper-13 input[type=checkbox]:hover:not(:checked):not(:disabled) {
      --bc: var(--border-hover);
    }
    .checkbox-wrapper-13 input[type=checkbox]:focus {
      box-shadow: 0 0 0 var(--focus);
    }
    .checkbox-wrapper-13 input[type=checkbox]:not(.switch) {
      width: 15px;
    }
    .checkbox-wrapper-13 input[type=checkbox]:not(.switch):after {
      opacity: 0;
      opacity: var(--o, 0);
    }
    .checkbox-wrapper-13 input[type=checkbox]:not(.switch):checked {
      --o: 1;
    }
    .checkbox-wrapper-13 input[type=checkbox] + label {
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      margin-left: 4px;
    }

    .checkbox-wrapper-13 input[type=checkbox]:not(.switch) {
      border-radius: 7px;
    }
    .checkbox-wrapper-13 input[type=checkbox]:not(.switch):after {
      width: 3px;
      height: 5px;
      border: 2px solid var(--active-inner);
      border-top: 0;
      border-left: 0;
      left: 5px;
      top: 3px;
      -webkit-transform: rotate(20deg);
              transform: rotate(20deg);
      -webkit-transform: rotate(var(--r, 20deg));
              transform: rotate(var(--r, 20deg));
    }
    .checkbox-wrapper-13 input[type=checkbox]:not(.switch):checked {
      --r: 43deg;
    }
  }

  .checkbox-wrapper-13 * {
    box-sizing: inherit;
  }
  .checkbox-wrapper-13 *:before,
  .checkbox-wrapper-13 *:after {
    box-sizing: inherit;
  }


/* button agregar-proceso */
.agregar-proceso{
    width: 100%;
    display: flex;
    margin-top: 15px;
    justify-content: center;  
}
.agregar-proceso button{
    color: white;
    background-color: #43CA40;
    padding: 6px 12px;
    border: none;
    border-radius: 15px;
    cursor: pointer;
    transition:ease-in-out;
    transition-duration: 200ms;
}

.agregar-proceso button:hover{
    background-color: #4ee34c;
}
/* ENTER */
.v_principal-enter,.v_exito-enter,.tabla-enter{
  opacity: 0;
}
.v_principal-enter-active,.v_exito-enter-active,.tabla-enter-active{
  opacity: 1;
  transition: opacity 500ms;
}
.v_principal-enter-done,.v_exito-enter-done,.tabla-enter-done{
  opacity: 1;
}
.fade-appear {
  opacity: 0;
  height: 0;
  overflow: hidden;
}

.fade-appear-active{
  opacity: 1;
  height: 60px;
  transition: opacity 175ms cubic-bezier(0.075, 0.82, 0.165, 1),height 175ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.fade-appear-done{
  opacity: 1;
  height: 60px;
}
/* EXIT */
.v_principal-exit, .v_exito-exit, .tabla-exit{
  opacity: 1;
}
.v_principal-exit-active,.v_exito-exit-active,.tabla-exit-active{
  opacity: 0;
  transition: opacity 500ms;
}
.v_principal-exit-done,.v_exito-exit-done,.tabla-exit-done{
  opacity: 0;
}



